import React, { Component, Fragment } from 'react';
import updateHandle from './index'
import { Added } from './AddedData';
import detectPhone from './detectPhone';

function ServingsList(props){
    return(
        <Fragment>
        <option value={props.value}>{props.dis}</option>
        </Fragment>
    )
}
class English extends Component {

    state = {phone: detectPhone(), grams: 100, garmInput: 1, servingInput: 100}
        
    gramHandle = (event) => {
        if (event.target.value < 1) {this.setState({garmInput: ''})
        } else if (event.target.value > 1000) {this.setState({garmInput: event.target.max})
        } else {this.setState({garmInput: event.target.value, grams: this.state.servingInput * event.target.value})}
    }
    servingHandle = (event) => this.setState({servingInput: event.target.value, grams: event.target.value * this.state.garmInput})

    calc = x => Number((((x * this.state.grams) / 100).toFixed(1)).toString());
    calcKcal = x => Number(((x * this.state.grams) / 100).toFixed(0));
    percentProtein = () => Math.floor((Number(this.props.protein) * 400)/Number(this.props.cal));
    percentFat = () => Math.floor((Number(this.props.fat) * 900)/Number(this.props.cal));
    percentCarb = () => (100 - (this.percentProtein() + this.percentFat()));
    servingMap = (arr) => {if (arr !== undefined) {return arr.map((e,index) => <ServingsList key={index} dis={e.dis[0]} value={e.value} />)}}
    
    PhoneView() {
        return(
            <div className='cardPhone' style={{backgroundImage: this.props.img }}>

                <button 
                    id='addButtonPc'
                    className='material-icons'
                    onClick={() => {
                                let merged = {...this.props, ...{gram: this.state.grams}};
                                Added.push(merged)
                                updateHandle();
                                document.getElementById('sumPhone').style.display='inline-flex';
                                window.scrollTo(0,0)
                            }}>add
                </button>

                <div className="kcalDetailsPhoneEn">
                    <div className='percentNumsPhone'>
                        <b className='b1'>{this.percentCarb()}%</b>
                        <b className='b2'>{this.percentProtein()}%</b>
                        <b className='b3'>{this.percentFat()}%</b><br />
                     </div>
                    <div id='bars'> 
                        <div id='barContainer'>
                            <div id='carbBar' style={{width: (this.percentCarb()*0.75)}}></div>
                            <b>CARBS</b>
                        </div>
                        <div id='barContainer'>
                            <div id='proteinBar' style={{width: (this.percentProtein()*0.75)}}></div>
                            <b>PROTEIN</b>
                        </div>
                        <div id='barContainer'>
                            <div id='fatBar' style={{width: (this.percentFat()*0.75)}}></div>
                            <b>FAT</b>
                        </div>
                    </div>
                    <h3>
                    Fat: <p>{this.calc(this.props.fat)} g</p>
                    <br />Carbohydrate: <p>{this.calc(this.props.carb)} g</p>
                    <br />Dietary fiber: <p>{this.calc(this.props.fiber)} g</p>
                    <br />Sugar: <p>{this.calc(this.props.sugar)} g</p>
                    <br />Protein: <p>{this.calc(this.props.protein)} g</p>
                    </h3>
                </div>

                <div className='titlePhoneEn'>
                    <h2>{this.props.name}</h2>
                    <h5>Calories: {this.calcKcal(this.props.cal)} kcal</h5>
                    <h6>Per {Number(((this.state.grams).toFixed(1)).toString())} gram</h6>
                    <input id="gram" type="number" min='1' max='1000' onChange={this.gramHandle} value={this.state.garmInput} />
                    <select id='servingPcEn' defaultValue={'100'} onChange={this.servingHandle}>
                        <option value="1">gram</option>
                        <option value="100">standart serving (100 g)</option>
                        {this.servingMap(this.props.serving)}
                    </select>
                </div>

            </div>
        )
    }

    PcView() {
        return( 
            <div className='cardPc' style={{backgroundImage: this.props.img }}>

                <button 
                    id='addButtonPc'
                    className='material-icons'
                    onClick={() => {
                                let merged = {...this.props, ...{gram: this.state.grams}};
                                Added.push(merged);
                                updateHandle();
                                if (Added.length !== 0) {document.getElementById('cardsContainer').style.marginLeft='200px'}
                                document.getElementById('sumPc').style.display = 'inline-flex'
                            }}>add
                </button>
                
                <div className="kcalDetailsPcEn">
                    <div className='percentNumsPc'>
                        <b className='b1'>{this.percentCarb()}%</b>
                        <b className='b2'>{this.percentProtein()}%</b>
                        <b className='b3'>{this.percentFat()}%</b><br />
                     </div>
                    <div id='bars'>
                        <div id='barContainer'>
                            <div id='carbBar' style={{width: (this.percentCarb()*0.75)}}></div>
                            <b>CARBS</b>
                        </div>
                        <div id='barContainer'>
                            <div id='proteinBar' style={{width: (this.percentProtein()*0.75)}}></div>
                            <b>PROTEIN</b>
                        </div>
                        <div id='barContainer'>
                            <div id='fatBar' style={{width: (this.percentFat()*0.75)}}></div>
                            <b>FAT</b>
                        </div>
                    </div>
                    <h3>
                    Fat: <p>{this.calc(this.props.fat)} g</p>
                    <br></br>Carbohydrate: <p>{this.calc(this.props.carb)} g</p>
                    <br></br>Dietary fiber: <p>{this.calc(this.props.fiber)} g</p>
                    <br></br>Sugar: <p>{this.calc(this.props.sugar)} g</p>
                    <br></br>Protein: <p>{this.calc(this.props.protein)} g</p>
                    </h3>
                </div>

                <div className='titlePcEn'>
                    <h4>{this.props.name}</h4>
                    <h5>Calories: {this.calcKcal(this.props.cal)} kcal</h5>
                    <h6>Per {Number(((this.state.grams).toFixed(1)).toString())} gram</h6>
                    
                    <input id="gram" type="number" min='1' max='1000' onChange={this.gramHandle} value={this.state.garmInput} />
                    <select id='servingPcEn' defaultValue={'100'} onChange={this.servingHandle}>
                        <option value="1">gram</option>
                        <option value="100">standart serving (100 g)</option>
                        {this.servingMap(this.props.serving)}
                    </select>
                
                </div>

            </div>
        )
    }

    render() {      
        if(this.state.phone) {return this.PhoneView()} else {return this.PcView()}
    }
}

export default English
export {ServingsList}