import React, { Component } from 'react';
import updateHandle from './index'
import {isAr} from './index'
import detectPhone from './detectPhone';

let Added = []

class AddedFruits extends Component {

    state = {isPhone: detectPhone()}

    detailRender = () => {
        if (isAr) {return this.props.cal + ' سعرة • ' + this.props.gram + 'غ '
        } else {return this.props.cal + ' kcal •  ' + this.props.gram + ' g'}
        
    }
    componentDidMount() {
        if (this.state.isPhone) {document.getElementById('itemsContainerPc').id='itemsContainerPhone'}
    }

    render () {
        return (
            <div id='itemsContainerPc'>
                <p className='itemNamePc'>{this.props.name}</p>

                <button 
                    id='deleteButtonPc'
                    className='material-icons'
                    onClick={() => {
                                var x= Added.findIndex((e) => e.id === this.props.id);
                                Added.splice(x, 1);
                                updateHandle();
                                if (Added.length === 0 && this.state.isPhone === false) {document.getElementById('sumPc').style.display = 'none';document.getElementById('cardsContainer').style.margin='0'}
                                if (Added.length === 0 && this.state.isPhone) {document.getElementById('sumPhone').style.display='none'}
                    }}>clear
                </button>

                <p id="itemDetailsPc">{this.detailRender()}</p>
            </div>
        )
    }
}
export default AddedFruits;
export {Added}

